
.card-content-container {
  position: relative;
}
.cover-when-draggable {
  top: 0px;
  left: 0px;
  border-radius: 6px;
  position: absolute;
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: center;
  background-color: rgba(164, 164, 164, 0.4);
  padding: 0px 16px;
  font-size: 16px;
  cursor: move;
}
.cover-when-draggable-content {
  max-width: 80%;
  max-height: 80%;
  margin: 0 auto;
  background-color: #15a5cc;
  color: white;
  border-radius: 12px;
  padding: 3px 12px;
}
.card-content-container,
.card-content-container h1,
.card-content-container h2,
.card-content-container h3,
.card-content-container h4,
.card-content-container h5
{
  font-family: 'Open Sans', sans-serif;
}
.card-content-container h1 {
  font-size: 19px;
}
.card-content-container h2 {
  font-size: 17px;
}
.card-content-container h3,
.card-content-container h4,
.card-content-container h5 {
  font-weight: bold;
  font-size: 15px;
}
.card-title {
  color: #15a5cc;
}




